import { HttpResponse, http } from "msw"
import { setupServer } from "msw/node"
import { afterAll, afterEach, beforeAll, beforeEach, jest } from "@jest/globals"
import { dbStub, mswStub, mswTrpcHandlers } from "../../msw"

const mockSVG = `<?xml version="1.0" standalone="no"?>
<!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 20010904//EN" "http://www.w3.org/TR/2001/REC-SVG-20010904/DTD/svg10.dtd">
<svg version="1.0" xmlns="http://www.w3.org/2000/svg"
 width="1280.000000pt" height="640.000000pt" viewBox="0 0 1280.000000 640.000000"
 preserveAspectRatio="xMidYMid meet">
</svg>`

const SVG = {
  get: () => http.get(`/*.svg`, () => HttpResponse.xml(mockSVG)),
}

const mswServer = setupServer(...mswTrpcHandlers, SVG.get())

global.HTMLElement.prototype.scrollTo = jest.fn()

beforeAll(() => mswServer.listen({}))
beforeEach(() => {
  jest.clearAllMocks()
})
afterEach(() => {
  dbStub.restoreInitialData()
  mswStub.apiClear()
  mswStub.clerkClear()
  mswStub.taClear()
  mswServer.resetHandlers()
})
afterAll(() => mswServer.close())
